body,
ul,
p,
button,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
  border: 0;
  margin-block-end: 0;
  margin-block-start: 0;
}

html,
body {
  color: #253757;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  font-family: "Muli", sans-serif;
  width: 100%;
}

h1 {
  font-weight: 900;
  font-size: 64px;
  line-height: 1.5;
}

h2 {
  font-weight: 900;
  font-size: 48px;
  line-height: 1;
}

h3 {
  font-weight: 900;
  font-size: 32px;
  line-height: 1.4;
}

@media screen and (max-width: 1023px) {
  .header {
    padding: 9px 45px 0 !important;
    background-image: none !important;
    background-color: $special-color;

    &__top {
      flex-direction: column;

      &_logo {
        margin-right: 0 !important;
        margin-bottom: 24px;
      }

      &_nav_spec {
        margin-left: 0 !important;
      }
    }

    &__wrapper {
      margin-left: 0 !important;
    }

    &__text {
      color: #253757;
    }
  }

  .nav__list {
    flex-direction: column;

    &_item {
      margin: 0 !important;
    }
  }

  .container {
    width: 280px !important;
    height: 210px !important;
    overflow: hidden;
  }
}
