@mixin button($back-color) {
  width: 226px;
  height: $button-height;
  border-radius: 64px;
  background-color: $back-color;
  cursor: pointer;
}

@mixin oval($width, $height, $color) {
  width: $width;
  height: $height;
  border: 4px solid $color;
  border-radius: 500px;
  box-sizing: border-box;
  position: absolute;
  transform: rotate(-65deg);
  opacity: 0.5;
}
